




















export default {
	data() {
		return {
			title: 'list-triplex-row',
			banner: {},
			htmlString: ""
		}
	},
	onShareAppMessage() {
		return {
			title: this.banner.title,
			path: '/pages/template/list2detail-detail/list2detail-detail?detailDate=' + JSON.stringify(this.banner)
		}
	},
	onLoad(e) {
		this.banner = JSON.parse(decodeURIComponent(e.detailDate));
		this.getDetail();
		uni.setNavigationBarTitle({
			title: this.banner.title
		})
	},
	methods: {
		getDetail() {
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + this.banner.post_id,
				success: (data) => {
					if (data.statusCode == 200) {
						this.htmlString = data.data.content.replace(/\\/g, "").replace(/<img/g, "<img style=\"display:none;\"");
					}
				},
				fail: () => {
					console.log('fail');
				}
			})
		}
	}
}



